import React, { useState, useEffect, useRef } from 'react';
import fp1 from './fp/1.png';
// import fp2 from './fp/2.png';
import fp3 from './fp/3.png';
import fp4 from './fp/4.png';
import fp5 from './fp/5.png';
import fp6 from './fp/6.png';
import fp7 from './fp/7.png';
import fp8 from './fp/8.png';
import fp9 from './fp/9.png';
// import fp10 from './fp/10.png';
// import fp11 from './fp/11.png';
// import fp12 from './fp/12.png';
import fp13 from './fp/13.png';
import fp14 from './fp/14.png';
import fp15 from './fp/15.png';
import fp16 from './fp/16.png';
import fp17 from './fp/17.png';
import fp18 from './fp/18.png';
import fp19 from './fp/19.png';
import Lng from './Lng/Lng.js';
// import logo from './logo.svg';
import fps from './logofp.png';
import VideoContentYT from './VideoContentYT';
import './App.css';

function App() {
    const localData = localStorage.getItem("data") ? JSON.parse(localStorage.getItem("data")) : null
    const [hashs, setHashs] = useState([]);
    const [userLang] = useState(navigator.language.substr(0, 2) || navigator.userLanguage.substr(0, 2));
    const [currentLang] = useState(Lng.hasOwnProperty(userLang) ? userLang : "en")
    document.documentElement.lang = currentLang;
    const [hashLength, setHashLength] = useState(localData ? localData.hashLength : 256);
    const [compareHash, setCompareHash] = useState(localData ? localData.compareHash : "")
    const [compareNewHash, setCompareNewHash]  = useState(localData ? localData.compareNewHash : "");
    const [chekedPrints, setChekedPrints] = useState(0);
    const [processingNum, setProcessingNum] = useState(localData ? localData.processingNum : 0);
    const [startTime, setStartTime] = useState(localData ? localData.startTime : new Date(0));
    const [currentTime, setCurrentTime] = useState(localData ? localData.currentTime : new Date(0));
    const [time, setTime] = useState(localData ? localData.time : new Date(0));
    const [imgFp, setImgFp ]= useState([fp1,fp3,fp4,fp5,fp6,fp7,fp8,fp9,fp13,fp14,fp15,fp16,fp17,fp18,fp19]);
    const [imgFpIndexFind, setImgFpIndexFind ]= useState(localData ? localData.imgFpIndexFind : 0);
    const [imgFpIndexTemp, setImgFpIndexTemp ]= useState(localData ? localData.imgFpIndexTemp : 0);
    const [firstLoad, setFirstLoad] = useState(localData ? localData.firstLoad : true);
    const [localSave, setLocalSave] = useState(localData ? localData.localSave : false);
    const [showInfo, setShowInfo] = useState(localData ? localData.showInfo : true);
    const [printFound, setPrintFound] = useState(localData ? localData.printFound : false);
    const [processing, setProcessing] = useState(localData ? localData.processing : false);
    const [showSettings, setShowSettings] = useState(localData ? localData.showSettings : false);

    const requestRef = React.useRef()

    function randomString(len, charSet) {
        charSet = charSet || 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        let randomString = '';
        for (let i = 0; i < len; i++) {
            let randomPoz = Math.floor(Math.random() * charSet.length);
            randomString += charSet.substring(randomPoz,randomPoz+1);
        }
        console.log(processing)
        return randomString;
    }

    function saveToLocalStorage() {
        if (localSave) {
            localStorage.setItem("data", JSON.stringify(
                {
                    hashLength: hashLength,
                    compareHash: compareHash,
                    compareNewHash: compareNewHash,
                    processingNum: processingNum,
                    startTime: startTime,
                    currentTime: currentTime,
                    time: time,
                    localSave: localSave,
                    imgFpIndexFind: imgFpIndexFind,
                    imgFpIndexTemp: imgFpIndexTemp,
                    firstLoad: firstLoad,
                    showInfo: showInfo,
                    showSettings: showSettings,
                    printFound: printFound,
                    processing: processing
                }
            ))
        }
    }

    function gen() {
        setCurrentTime(Date.now);
        // console.log(startTime+" - "+ currentTime)
        setTime(currentTime - startTime);
        let newHash = randomString(hashLength);
        // let lhashs = [];
        // if (sessionStorage.getItem("hashs")) {
        //     lhashs = JSON.parse(sessionStorage.getItem("hashs"));
        // }
        // let uniqHash = false;
        // let uniqHashAt = 0;
        // while (uniqHash) {
        //     uniqHashAt = uniqHashAt + 1;
        //     if (!lhashs.hasOwnProperty(newHash)) {
        //         uniqHash = true;
        //     } else {
        //         newHash = randomString(256);
        //         console.log("Dublicate hash, get new: "+newHash)
        //     }
        //     if (uniqHashAt > 1000) {
        //         uniqHash = true;
        //     }
        // }
        // console.log("new hash: "+newHash)
        setCompareNewHash(newHash);
        setImgFpIndexTemp(Math.floor(Math.random() * Math.floor(11)))
        if (compareHash === newHash) {
            setProcessing(false);
            setPrintFound(true)
            setImgFpIndexTemp(imgFpIndexFind)
        } else {
            setProcessingNum(processingNum + 1)
            // lhashs.push(newHash);
            // sessionStorage.setItem("hashs", JSON.stringify(lhashs));
            // setHashs(hashs);
        }
        saveToLocalStorage()
    }

    useEffect(() => {
        if (processing) {
            requestRef.current = requestAnimationFrame(gen);
            return () => cancelAnimationFrame(requestRef.current);
        } else {
            saveToLocalStorage()
        }
    });
    // setHashs(hashs.push(randomString(256)))
    // setHashs(hashs.push(randomString(256)))
    // console.log(randomString(256));
    // alert(Math.random().toString(36).substring(2, 128))

    function getTimerString(milSec) {
        const days = Math.floor(milSec / 86400000);
        if (days === 0) {
            return new Date(time).toISOString().slice(11, -1);
        } else {
            return days+Lng[currentLang].days+new Date(time).toISOString().slice(11, -1);
        }
    }

  return (
    <div className="App">
      <header className="App-header">
          <div className="LogoFP">
            <img style={{maxWidth: "100%", maxHeight: "100%"}} src={fps} />
            <div style={{display: "inline-grid"}}>
                <span className="LogoFPText">Fingerprint checker</span>
                <span className="LogoFPTextBy">by Octanium (octanium91@gmail.com)</span>
            </div>
          </div>
          {showInfo?
              <div className="infoText">
                  <span className="spanInfoText arrowUp" onClick={() => {
                      setShowInfo(!showInfo)
                      saveToLocalStorage()
                  }}>{Lng[currentLang].hideInfoText}</span>
                  <VideoContentYT src="IqEoZiX_G3c" params={{autoPlay: true}} />
                  <p>{Lng[currentLang].infoText}</p>
              </div>:
              <div className="infoText">
                  <span className="spanInfoText arrowDown" onClick={() => {
                      setShowInfo(!showInfo)
                      saveToLocalStorage()
                  }}>{Lng[currentLang].showInfoText}</span>
              </div>}
          {showSettings?
              <div className="settings">
                  <span className="spanSettings arrowUp" onClick={() => {
                      setShowSettings(!showSettings)
                      saveToLocalStorage()
                  }}>{Lng[currentLang].hideSettings}</span>
                  <div className="checkbox">
                      <label>
                          <input
                              className="checkboxInput"
                              type="checkbox"
                              checked={localSave}
                              onChange={() => {
                                  if (localSave) {
                                      localStorage.removeItem("data")
                                      setLocalSave(false)
                                  } else {
                                      setLocalSave(true)
                                      saveToLocalStorage()
                                  }
                              }}
                          />
                          {Lng[currentLang].saveState}
                      </label>
                  </div>
                  <div className="input-group mb-3">
                      <div className="input-group-prepend">
                          <label className="input-group-text" style={{fontSize: "1.7rem"}} htmlFor="inputGroupSelect01">{Lng[currentLang].difficulty}</label>
                      </div>
                      <select className="custom-select" style={{fontSize: "1.7rem"}} disabled={processing} id="inputGroupSelect01" defaultValue={hashLength}
                              onChange={(event) => {setHashLength(Number(event.target.value))}}>
                          <option value="256">{"256 "+Lng[currentLang].difficultyCharacters}</option>
                          <option value="128">{"128 "+Lng[currentLang].difficultyCharacters}</option>
                          <option value="64">{"64 "+Lng[currentLang].difficultyCharacters}</option>
                          <option value="32">{"32 "+Lng[currentLang].difficultyCharacters}</option>
                          <option value="16">{"16 "+Lng[currentLang].difficultyCharacters}</option>
                      </select>
                  </div>
                  {processing ? <p>{Lng[currentLang].difficultyMess}</p> : <div/>}
              </div>:
              <div className="settings">
                  <span className="spanSettings arrowDown" onClick={() => {
                      setShowSettings(!showSettings)
                      saveToLocalStorage()
                  }}>{Lng[currentLang].showSettings}</span>
              </div>}
          {!firstLoad?
              <div className="sumInfo">
              <span>{Lng[currentLang].timeSpent+": "+getTimerString(time)}</span>
              <span>{Lng[currentLang].checkedFingerprints+": "+processingNum}</span>
          </div>:
              <div></div>}
          {!firstLoad?
              !printFound? <div className={"fingers"}>
              <div className="fingerP">
                  <p>{Lng[currentLang].generatedFingerprint}</p>
                  <img src={imgFp[imgFpIndexFind]} />
                  {/*<p>{compareHash}</p>*/}
              </div>
              <div className="fingerP">
                  <p>{Lng[currentLang].tryGenSame}</p>
                  <img src={imgFp[imgFpIndexTemp]} />
                  {/*<p>{compareNewHash}</p>*/}
              </div>
                </div>
              :<div className="fingerP">
                  <p>{Lng[currentLang].wowYouDidIt}</p>
                  <img src={imgFp[imgFpIndexFind]} />
                  {/*<p>{compareHash}</p>*/}
              </div>:
              <div></div>}
          {/*<input value={compareHash}/>*/}
          {/*<input value={compareNewHash}/>*/}
          <button className={processing ? "btn btn-danger startBtn":"btn btn-success startBtn"} onClick={() => {
              // if (!processing) {
              //     sessionStorage.setItem("hashs", [])
              // }
              if (processing) {
                  setProcessing(false);
              } else {
                  setStartTime(Date.now);
                  setFirstLoad(false);
                  setPrintFound(false)
                  setShowInfo(false)
                  setImgFpIndexFind(Math.floor(Math.random() * Math.floor(11)));
                  setCompareHash(randomString(hashLength));
                  setProcessingNum(0)
                  setProcessing(true);
              }
              saveToLocalStorage()
          }}>{processing ? Lng[currentLang].btnStop : Lng[currentLang].btnStart}</button>
        {/*<img src={logo} className="App-logo" alt="logo" />*/}
        {/*<p>*/}
        {/*  Edit <code>src/App.js</code> and save to reload.*/}
        {/*</p>*/}
        {/*<a*/}
        {/*  className="App-link"*/}
        {/*  href="https://reactjs.org"*/}
        {/*  target="_blank"*/}
        {/*  rel="noopener noreferrer"*/}
        {/*>*/}
        {/*  Learn React*/}
        {/*</a>*/}
      </header>
    </div>
  );
}

export default App;
